.excel-wrapper {
  /* padding: 20px; */
  height: calc(100vh - 40px);
  overflow: hidden;
}
.excel-main {
  border: 1px solid #887e7e;
  height: 100%;
}
.excel-right {
  border-left: 1px solid #887e7e;
  padding: 20px;
}

.excel-left .rowBgColor {
    background-color: rgba(0,0,0);
}
.excel-left .ant-table-tbody > tr.ant-table-row:hover > td {
  background: rgba(0,0,0);
}
